/**
 * ‰ª£Â∏Å‰∫§Êç¢ÁªÑ‰ª∂
 * Áî®Êà∑ÂèØ‰ª•‰ΩøÁî®BNBË¥≠‰π∞SM‰ª£Â∏Å
 */

import React, { useState, useEffect } from 'react';
import { useWallet } from '@/contexts/WalletContext';
import { NumberInput } from '@/components/ui/SecureInput';

interface ExchangeStats {
  currentPrice: string;
  totalSold: string;
  totalRemaining: string;
  currentRound: number;
  nextRoundPrice: string;
}

export default function TokenExchange() {
  const { wallet, updateBalances } = useWallet();
  const [bnbAmount, setBnbAmount] = useState<string>('');
  const [smAmount, setSmAmount] = useState<string>('');
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [stats, setStats] = useState<ExchangeStats>({
    currentPrice: '0.000833',
    totalSold: '1000000',
    totalRemaining: '9000000',
    currentRound: 1,
    nextRoundPrice: '0.000975'
  });

  // Ëé∑Âèñ‰∫§Êç¢ÁªüËÆ°‰ø°ÊÅØ
  useEffect(() => {
    const fetchStats = async () => {
      try {
        // Ê®°ÊãüAPIË∞ÉÁî®Ëé∑ÂèñÂÆûÊó∂Êï∞ÊçÆ
        const mockStats: ExchangeStats = {
          currentPrice: '0.000833',
          totalSold: '1250000',
          totalRemaining: '8750000',
          currentRound: 1,
          nextRoundPrice: '0.000975'
        };
        setStats(mockStats);
      } catch (error) {
        console.error('Ëé∑Âèñ‰∫§Êç¢ÁªüËÆ°Â§±Ë¥•:', error);
      }
    };

    fetchStats();
    const interval = setInterval(fetchStats, 30000); // ÊØè30ÁßíÊõ¥Êñ∞‰∏ÄÊ¨°

    return () => clearInterval(interval);
  }, []);

  // ËÆ°ÁÆóSM‰ª£Â∏ÅÊï∞Èáè
  const calculateSMAmount = (bnb: string): string => {
    if (!bnb || isNaN(Number(bnb))) return '0';
    const bnbNum = parseFloat(bnb);
    const priceNum = parseFloat(stats.currentPrice);
    return (bnbNum / priceNum).toFixed(6);
  };

  // Â§ÑÁêÜBNBËæìÂÖ•ÂèòÂåñ
  const handleBnbChange = (value: string) => {
    setBnbAmount(value);
    setSmAmount(calculateSMAmount(value));
  };

  // Â§ÑÁêÜSMËæìÂÖ•ÂèòÂåñ
  const handleSmChange = (value: string) => {
    setSmAmount(value);
    if (!value || isNaN(Number(value))) {
      setBnbAmount('0');
      return;
    }
    const smNum = parseFloat(value);
    const priceNum = parseFloat(stats.currentPrice);
    setBnbAmount((smNum * priceNum).toFixed(8));
  };

  // ËÆæÁΩÆÊúÄÂ§ßBNBÊï∞Èáè
  const handleMaxBnb = () => {
    if (wallet.balance?.bnb) {
      const maxBnb = parseFloat(wallet.balance.bnb);
      const adjustedMax = Math.max(0, maxBnb - 0.001).toFixed(6); // ‰øùÁïôgasË¥π
      handleBnbChange(adjustedMax);
    }
  };

  // ÊâßË°å‰∫§Êç¢
  const handleExchange = async () => {
    if (!bnbAmount || parseFloat(bnbAmount) <= 0) {
      alert('ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑBNBÊï∞Èáè');
      return;
    }

    if (!wallet.isConnected) {
      alert('ËØ∑ÂÖàËøûÊé•Èí±ÂåÖ');
      return;
    }

    const bnbNum = parseFloat(bnbAmount);
    const currentBnbBalance = wallet.balance ? parseFloat(wallet.balance.bnb) : 0;
    
    if (bnbNum > currentBnbBalance) {
      alert('BNB‰ΩôÈ¢ù‰∏çË∂≥');
      return;
    }

    setIsLoading(true);
    try {
      // Ê®°Êãü‰∫§Êç¢ËøáÁ®ã
      await new Promise(resolve => setTimeout(resolve, 3000));
      
      alert(`ÊàêÂäüË¥≠‰π∞ ${smAmount} SM‰ª£Â∏ÅÔºÅ`);
      setBnbAmount('');
      setSmAmount('');
      
      // Êõ¥Êñ∞‰ΩôÈ¢ù
      await updateBalances();
    } catch (error) {
      console.error('‰∫§Êç¢Â§±Ë¥•:', error);
      alert('‰∫§Êç¢Â§±Ë¥•ÔºåËØ∑ÈáçËØï');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="max-w-2xl mx-auto">
      {/* ÁªüËÆ°‰ø°ÊÅØ */}
      <div className="bg-white rounded-lg shadow-md p-6 mb-6">
        <h2 className="text-2xl font-bold text-center mb-6">‰ª£Â∏Å‰∫§Êç¢</h2>
        
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-6">
          <div className="text-center">
            <div className="text-2xl font-bold text-blue-600">{stats.currentPrice}</div>
            <div className="text-sm text-gray-600">ÂΩìÂâç‰ª∑Ê†º (BNB)</div>
          </div>
          <div className="text-center">
            <div className="text-2xl font-bold text-green-600">{parseInt(stats.totalSold).toLocaleString()}</div>
            <div className="text-sm text-gray-600">Â∑≤ÂîÆÂá∫</div>
          </div>
          <div className="text-center">
            <div className="text-2xl font-bold text-purple-600">{parseInt(stats.totalRemaining).toLocaleString()}</div>
            <div className="text-sm text-gray-600">Ââ©‰Ωô</div>
          </div>
          <div className="text-center">
            <div className="text-2xl font-bold text-orange-600">{stats.currentRound}</div>
            <div className="text-sm text-gray-600">ÂΩìÂâçËΩÆÊ¨°</div>
          </div>
        </div>
      </div>

      {/* ‰∫§Êç¢ÁïåÈù¢ */}
      <div className="bg-white rounded-lg shadow-md p-6">
        <div className="space-y-4">
          {/* BNBËæìÂÖ• */}
          <div>
            <div className="flex justify-between items-center mb-2">
              <label className="text-sm font-medium text-gray-700">ÊîØ‰ªò (BNB)</label>
              {wallet.isConnected && wallet.balance && (
                <div className="text-xs text-gray-500">
                  ‰ΩôÈ¢ù: {parseFloat(wallet.balance.bnb).toFixed(6)} BNB
                  <button
                    onClick={handleMaxBnb}
                    className="ml-2 text-blue-500 hover:text-blue-700"
                  >
                    ÊúÄÂ§ß
                  </button>
                </div>
              )}
            </div>
            <NumberInput
              value={bnbAmount}
              onChange={(e) => handleBnbChange(e.target.value)}
              placeholder="0.0"
              minValue={0.000001}
              maxValue={1000}
              className="w-full text-lg"
            />
          </div>

          {/* ‰∫§Êç¢ÁÆ≠Â§¥ */}
          <div className="flex justify-center">
            <div className="w-8 h-8 bg-gray-100 rounded-full flex items-center justify-center">
              <svg className="w-4 h-4 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 14l-7 7m0 0l-7-7m7 7V3" />
              </svg>
            </div>
          </div>

          {/* SMËæìÂá∫ */}
          <div>
            <div className="flex justify-between items-center mb-2">
              <label className="text-sm font-medium text-gray-700">Ëé∑Âæó (SM)</label>
              {wallet.isConnected && wallet.balance?.sm && (
                <div className="text-xs text-gray-500">
                  ‰ΩôÈ¢ù: {parseFloat(wallet.balance.sm).toFixed(6)} SM
                </div>
              )}
            </div>
            <NumberInput
              value={smAmount}
              onChange={(e) => handleSmChange(e.target.value)}
              placeholder="0.0"
              minValue={0}
              className="w-full text-lg"
            />
          </div>

          {/* ‰∫§Êç¢‰ø°ÊÅØ */}
          {bnbAmount && smAmount && (
            <div className="bg-blue-50 rounded-lg p-4">
              <div className="text-sm space-y-1">
                <div className="flex justify-between">
                  <span>‰∫§Êç¢Áéá:</span>
                  <span>1 BNB = {(1 / parseFloat(stats.currentPrice)).toFixed(2)} SM</span>
                </div>
                <div className="flex justify-between">
                  <span>È¢ÑËÆ°gasË¥π:</span>
                  <span>~0.001 BNB</span>
                </div>
                <div className="flex justify-between">
                  <span>ÊªëÁÇπÂÆπÂøç:</span>
                  <span>0.5%</span>
                </div>
              </div>
            </div>
          )}

          {/* ‰∫§Êç¢ÊåâÈíÆ */}
          <button
            onClick={handleExchange}
            disabled={isLoading || !bnbAmount || !wallet.isConnected}
            className={`w-full py-4 px-6 rounded-lg font-medium text-lg ${
              isLoading || !bnbAmount || !wallet.isConnected
                ? 'bg-gray-300 text-gray-500 cursor-not-allowed'
                : 'bg-blue-500 text-white hover:bg-blue-600'
            }`}
          >
            {isLoading ? '‰∫§Êç¢‰∏≠...' : wallet.isConnected ? 'Á°ÆËÆ§‰∫§Êç¢' : 'ËØ∑ËøûÊé•Èí±ÂåÖ'}
          </button>
        </div>
      </div>

      {/* ‰∏ã‰∏ÄËΩÆ‰ª∑Ê†ºÊèêÁ§∫ */}
      <div className="mt-4 p-4 bg-yellow-50 rounded-lg">
        <div className="text-sm text-yellow-800">
          üí° ‰∏ã‰∏ÄËΩÆ‰ª∑Ê†ºÂ∞Ü‰∏äÊ∂®Ëá≥ {stats.nextRoundPrice} BNBÔºåË∂äÊó©Ë¥≠‰π∞Ë∂ä‰ºòÊÉ†ÔºÅ
        </div>
      </div>
    </div>
  );
}
